header {
  color: var(--color-white);
  min-height: 230px;
  padding: 40px 20px 0;
}

header#main-header {
  background: url("main-header-bg-mobile.b1e6ba31.jpg") 50% / cover;
}

header#library-header {
  background: url("library-header-bg-mobile.ce5fe27e.jpg") 50% / cover;
}

nav {
  justify-content: space-between;
  align-items: center;
  max-width: 1280px;
  height: 24px;
  margin: auto;
  display: flex;
}

#logo {
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

#logo:hover {
  opacity: .8;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  transform: translateY(-2px);
}

#logo a {
  color: var(--color-white);
  text-align: center;
  align-items: center;
  gap: 10px;
  font-size: 30px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

#logo img {
  width: 24px;
  height: 24px;
}

#logo a span {
  display: none;
}

ul#nav-menu {
  text-transform: uppercase;
  gap: 20px;
  list-style: none;
  display: flex;
}

ul#nav-menu li a {
  text-transform: uppercase;
  text-align: center;
  color: var(--color-white);
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

.link-menu {
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.link-menu:hover, .link-menu:active, .link-menu:focus {
  opacity: .8;
  color: var(--color-red);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.link-menu:after {
  content: "";
  background: none;
  width: 0;
  height: 3px;
  margin: auto;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 16px;
}

.link-menu:hover:after, .link-menu.active-nav:after {
  background: var(--color-red);
  width: 100%;
}

.active-link-menu:after {
  content: "";
  background: var(--color-red);
  width: 100%;
  height: 3px;
  margin: auto;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 16px;
}

.search-form {
  text-align: center;
  width: 100%;
  max-width: 280px;
  height: 20px;
  margin: 54px auto 0;
  position: relative;
}

.search-form input[type="text"] {
  background-color: unset;
  color: var(--color-white);
  border: none;
  border-bottom: .5px var(--color-white) solid;
  font-size: 1em;
  font-family: var(--font-family);
  border-radius: 0;
  width: 100%;
  padding: 0 0 5px;
  font-size: 14px;
  font-weight: 400;
}

.search-form input::placeholder {
  font-family: var(--font-family);
  color: var(--color-white);
  font-size: 14px;
  font-weight: 400;
}

.search-form button {
  background: unset;
  border: none;
  padding: 0;
  position: absolute;
  top: 2px;
  right: 0;
}

.search-form button:hover {
  cursor: pointer;
}

.warning {
  font-family: var(--font-family);
  color: var(--color-red);
  white-space: unset;
  text-align: left;
  width: 100%;
  max-width: 320px;
  margin: 0;
  padding: 0 20px;
  font-size: 14px;
  font-weight: 400;
  position: absolute;
  top: 154px;
  left: 50%;
  transform: translateX(-50%);
}

.btns-container {
  justify-content: center;
  gap: 20px;
  margin-top: 60px;
  display: flex;
}

.btns-container button {
  font-family: var(--font-family);
  text-transform: uppercase;
  text-align: center;
  color: var(--color-white);
  background: unset;
  border: 1px var(--color-white) solid;
  border-radius: 5px;
  width: 127px;
  height: 44px;
  font-size: 12px;
  font-weight: 500;
  line-height: 133%;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.library-btn {
  background: unset;
  border: 1px var(--color-white) solid;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.btns-container button.library-btn-active {
  background: var(--color-orange);
  border: 1px var(--color-orange) solid;
  cursor: pointer;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 8px 43px #ff6b0199;
}

.watch-btn:hover, .queue-btn:hover {
  background: var(--color-orange);
  border: 1px var(--color-orange) solid;
  cursor: pointer;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  transform: translateY(-2px);
  box-shadow: 0 8px 43px #ff6b0199;
}

@media only screen and (width >= 420px) {
  header {
    min-height: 216px;
    padding: 40px 32px 0;
  }

  header#main-header {
    background: url("main-header-bg-tablet.07b8305f.jpg") 50% / cover;
  }

  header#library-header {
    background: url("library-header-bg-tablet.805b703a.jpg") 50% / cover;
  }

  nav {
    height: 35px;
  }

  #logo a span {
    display: block;
  }

  .search-form {
    max-width: 336px;
    margin: 40px auto 0;
  }

  .warning {
    text-align: center;
    white-space: nowrap;
    width: 100%;
    max-width: 394px;
    padding: 0;
    top: 155px;
  }

  .btns-container {
    gap: 32px;
    margin-top: 40px;
  }

  .btns-container button {
    width: 150px;
    height: 42px;
  }
}

@media only screen and (width >= 768px) {
  header#main-header {
    background: url("main-header-bg-desktop.e68ace4d.jpg") 50% / cover;
  }

  header#library-header {
    background: url("library-header-bg-desktop.89e6fb21.jpg") 50% / cover;
  }

  nav {
    height: 34px;
  }

  .search-form {
    max-width: 394px;
  }

  .btns-container {
    gap: 16px;
    margin-top: 40px;
  }

  .btns-container button {
    width: 148px;
    height: 42px;
  }
}

@media screen and (width >= 1157px) {
  .cards-list {
    flex-wrap: wrap;
    gap: 32px 16px;
    margin: 60px 30px;
    display: flex;
  }
}

@media screen and (width >= 768px) and (width <= 1157px) {
  .cards-list {
    flex-wrap: wrap;
    gap: 32px;
    margin: 60px 32px;
    display: flex;
  }
}

@media screen and (width <= 767px) {
  .cards-list {
    flex-wrap: wrap;
    gap: 32px;
    margin: 20px 20px 40px;
    display: flex;
  }
}

:root {
  --font-family: "Roboto", sans-serif;
  --color-red: #ff001b;
  --color-orange: #ff6b01;
  --color-white: #fff;
}

* {
  font-family: var(--font-family);
}
